import React, { Component } from 'react';

class PopulationValue extends Component{
    constructor(props){
        super(props)
    }

    render(){
       
        if(this.props.popValue === 'unknown')
        {
            return(
            
                <td> --- </td>
        );

        }
        else{
            const planetVal = (parseInt(this.props.popValue) / 100000)
                if(planetVal < 100 ){
                    return ( <td> <i>{this.props.popValue} </i></td>)
                }
                else if(planetVal < 1000){
                    return (<td><h5>{this.props.popValue}</h5></td>)
                }
                else if(planetVal < 2000){
                    return (<td><h3>{this.props.popValue}</h3></td>)
                }
                else if(planetVal < 2500){
                    return (<td><h2>{this.props.popValue}</h2></td>)
                }
                else if(planetVal < 4000){
                    return (<td><h1>{this.props.popValue}</h1></td>)
                }
                else {
                    
                    return (<td><h1><i> {this.props.popValue} </i></h1></td>)
                    
                }

              
        }
      
    }

}

export default PopulationValue


